@media(min-width: 320px) {
    
    .emoList {
        display: flex;
        flex-flow: column wrap;
        width: 100%;
        align-items: start;
        justify-content: space-evenly;
        gap: 2em;
        margin: 0;
        list-style: none;
    }
    .emoList:nth-child(1){
        margin-bottom: 2em;
    }
    .emoList li {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    .contentBlock {
        padding-top: 2em;
        place-content: space-evenly;
        display: grid;
        grid-template-columns: 23% 1fr 23%;
        grid-auto-flow: column;
        place-content: center;
    }
    .mobileCircleWrapper {
        display: block;
    }
    .popupBlock {
        position: absolute;
        display: block;
        padding: 5% 5% 10% 5%;
        top: 4em;
        left: 0;
        right: 0;
        bottom: 0;
        width: auto;
        min-height: 500px;
        background-color: rgb(204, 222, 229);
        overflow-y: scroll;
    
    }
    .innerList {
        display: none;
        row-gap: 10px;
        padding-top: 5%;
        padding-left: 0;
        list-style: none;
        font-weight: 500;
    }
    .innerItem {
        align-items: start;
    }
}
@media(max-width: 768px) {
    .mobileCircleWrapper {
        display: block;
        order: 1;
    }
    .circleWrapper {
        display: none;
    }
    .emoList {
        padding: 0;
        order: 2;
    }
}
@media(min-width: 769px) {
    .emoList {
        justify-content: start;
        flex-flow: row; 
    }
    .mobileCircleWrapper {
        display: none;
    }
    .popupBlock {
        width: 70%;
        left: 50%;
        bottom: auto;
        transform: translateX(-50%);
        border-radius: 10px;
    }
    .innerList {
        padding-left: 5%;
    }
}
@media(min-width: 1044px) {
    .emoList {
        padding: 2em;
    }
    .popupBlock {
        width: 40%;
    }
}

@media(max-width: 1288px) {
    
    .secondList{
        order: 2;
    }
    .contentBlock {
        display: flex;
        flex-flow: row wrap;
    }
    .emoHeading {
        width: 80%;
        padding: 1em;
        font-size: 16px;
    }
    .circleWrapper {
        order: 3;
        justify-content: center;
    }
}

@media(min-width: 1288px) {
    .emoList {
        justify-content: start;
        align-items: center;
        flex-flow: column; 
    }
    .emoHeading {
        width: 200px;
    }
    
} 
/* aside */



.open {
    display: flex;
    flex-flow: column;
}
.emoHeading {
    margin: 0;
    padding: 10px;
    font-size: 20px;
    cursor: pointer;
    transition: all .4s ease;
    text-shadow: 0 0 1px black;
    border-radius: 10px;
    text-align: center;
    text-transform: uppercase;
    color: white
}

.green {
    background-color: rgba(5, 138, 71, 0.765);
    color: white;
}
.innerItem {
    transition: all .4s ease;
    cursor: pointer;
}
.green +  .innerList > .innerItem:hover{
    color: #058a48;
}
.blue {
    background-color: rgba(2, 147, 187, 0.765);
}
.blue +  .innerList > .innerItem:hover{
    color: #0294bb;
}
.purple {
    background-color: rgba(144, 103, 173, 0.765);
}
.purple +  .innerList > .innerItem:hover{
    color: #9067ad;
}
.red {
    background-color: rgba(229, 36, 87, 0.765);
}
.red +  .innerList > .innerItem:hover{
    color: #e6255a;
}
.orange {
    background-color: rgba(230, 139, 34, 0.765);
}
.orange +  .innerList > .innerItem:hover{
    color: #e68b22;
}
.yellow {
    background-color: rgba(240, 203, 20, 0.765);
}
.yellow +  .innerList > .innerItem:hover{
    color: #cfaf0f;
}
.green:hover {
    background-color: #036c38;

}
.blue:hover {
    background-color: #0581a4;
}
.purple:hover {
    background-color: #6c4d82;
}
.red:hover {
    background-color: #bb1d47;
}
.orange:hover {
    background-color: #d87606;
}
.yellow:hover {
    background-color: #d6b408;
}
/* circle */
.chart {
    overflow: visible;
}
.circleWrapper {
    margin: 0 auto;
    padding-bottom: 2em;
}
.center {
    fill: rgb(163, 163, 215);
}
.circle {
    display: flex;
    position: relative;
    padding-top: 2em;
    box-sizing: border-box;
}

.mobileCircleWrapper .circle {
    justify-content: center;
}
.emoList {
    padding: 0;
}
.mobileCircleWrapper .unit {
    stroke-width: 75px;
}
.mobileCircleWrapper .unit:hover {
    stroke-width: 125px;
}
.mobileCircleWrapper .unit:nth-child(1) {
    stroke-dasharray: 60 471;
}
.mobileCircleWrapper .unit:nth-child(2) {
    stroke-dasharray: 60 471;
    stroke-dashoffset: -60;
}
.mobileCircleWrapper .unit:nth-child(3) {
    stroke-dasharray: 60 471;
    stroke-dashoffset: -120;
}
.mobileCircleWrapper .unit:nth-child(4) {
    stroke-dasharray: 60 471;
    stroke-dashoffset: -180;
}
.mobileCircleWrapper .unit:nth-child(5) {
    stroke-dasharray: 60 471;
    stroke-dashoffset: -240;
}
.mobileCircleWrapper .unit:nth-child(6) {
    stroke-dasharray: 60 471;
    stroke-dashoffset: -300;
}
.mobileCircleWrapper .unit:nth-child(7) {
    stroke-dasharray: 60 471;
    stroke-dashoffset: -360;
}
.mobileCircleWrapper .unit:nth-child(8) {
    stroke-dasharray: 60 471;
    stroke-dashoffset: -420;
}
.unit {
    fill: none;
    stroke-width: 350px;
    z-index: 2;
    cursor: pointer;
    transition: all .4s ease;
    
}
.unit:hover {
    stroke-width: 400px;
}
