/* text */
.list {
    list-style: none;
    text-align: center;
    display: flex;
    flex-flow: column;
    row-gap: 25px;
    width: 100%;
    height: 100%;
    font-size: 17px;
    margin: 0;
    font-weight: 500;
    color: white;
    text-shadow: 0 0 3px black;
    cursor: pointer;
}

.listGreen {
    transform: rotate(-66deg);
}
.listLightGreen {
    transform: rotate(-28deg);
}
.listBlue{
    transform: rotate(24deg);
}
.listLightBlue {
    display: flex;
    transform: rotate(66deg);
    justify-content: center;
}
.listPurple {
    display: flex;
    transform: rotate(-65deg);
    justify-content: center;
}
.listRed {
    display: flex;
    transform: rotate(-21deg);
    justify-content: center;
}
.listOrange {
    display: flex;
    transform: rotate(23deg);
    justify-content: center;
}
.listYellow {
    display: flex;
    transform: rotate(67deg);
    justify-content: center;
}
/* tooltip */
.tooltipOpen {
    position: absolute;
    display: flex;
    flex-flow: column;
    padding: 1em;
    width: 100px;
    min-height: 30px;
    background-color: rgb(204, 222, 229);

}
.tooltip {
    display: none;
}
/* popup */
.popup {
    position: fixed;
    display: none;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .5);
    overflow: hidden;
}

.popupHeading {
    margin: 0;
    text-align: center;
    font-size: 25px;
    text-transform: capitalize;
    margin-bottom: 30px;
}
.popupText {
    margin: 0;
    padding: 30px;
    font-size: 20px;
}
.popup.openPopup {
    display: block;
}
.info {
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 20px;
    font-weight: 500;
}
